<template>
  <div id="orders">
    <!--  -->
    <Crumbs :crumbs="this.crumbs"></Crumbs>

    <!--  -->

    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="10">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="orderList" border style="width: 100%">
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column prop="order_price" label=""></el-table-column>
        <el-table-column prop="order_pay" label="">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.order_pay !== '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">{{scope.row.create_time | dateFormat}}</template>
        </el-table-column>
        <el-table-column label="">
          <template>
            <el-button type="primary" icon="el-icon-edit" @click="handleAddress"></el-button>
            <el-button type="success" icon="el-icon-location" @click="handleLogistics"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="paramsInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="paramsInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totals"
      ></el-pagination>

      <!--  -->
      <el-dialog title="" :visible.sync="dialogVisibleOrders" width="50%" @close="handleClose">
        <el-form
          :model="ruleFormOrders"
          :rules="rulesOrders"
          ref="OrdersRuleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <!--  -->
          <el-form-item label="/" prop="address">
            <el-cascader
              v-model="ruleFormOrders.address"
              :options="citydata"
              :props="{ expandTrigger: 'hover' }"
            ></el-cascader>
          </el-form-item>

          <!--  -->
          <el-form-item label="" prop="detailed">
            <el-input v-model="ruleFormOrders.detailed"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisibleOrders = false"> </el-button>
          <el-button type="primary" @click="dialogVisibleOrders = false"> </el-button>
        </span>
      </el-dialog>

      <!--  -->

      <el-dialog title="" :visible.sync="dialogVisibleLogistics" width="50%">
        <!--  -->
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in logisticsList"
            :key="index"
            :timestamp="activity.time"
          >{{activity.context}}</el-timeline-item>
        </el-timeline>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import Crumbs from '../crumbs.vue'
import citydata from '../../assets/list/citydata.js'
export default {
  data() {
    return {
      // 
      crumbs: [
        {
          name: '',
          id: 0
        },
        {
          name: '',
          id: 1
        }
      ],

      // 
      orderList: [],

      // 
      totals: 0,

      // 
      paramsInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      rulesOrders: {
        address: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        detailed: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      dialogVisibleOrders: false,

      // 
      ruleFormOrders: {
        address: '',
        detailed: ''
      },
      // /
      citydata: citydata,

      // 
      dialogVisibleLogistics: false,

      // 
      logisticsList: []
    }
  },
  components: {
    Crumbs
  },
  created() {
    this.getOrderList()
  },
  methods: {
    // 
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.paramsInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.orderList = res.data.goods
      this.totals = res.data.total
      this.$message.success('')
    },

    //  pagesize  
    handleSizeChange(newPagesize) {
      this.paramsInfo.pagesize = newPagesize
      this.getOrderList()
    },

    //  pagenum   
    handleCurrentChange(newPagesize) {
      this.paramsInfo.pagenum = newPagesize
      this.getOrderList()
    },

    // 
    handleAddress() {
      this.dialogVisibleOrders = true
    },

    // 
    handleClose() {
      this.$refs.OrdersRuleForm.resetFields()
    },

    //  
    async handleLogistics() {
      this.dialogVisibleLogistics = true

      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.logisticsList = res.data
    }
  }
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
